.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 10s linear;
          animation: App-logo-spin infinite 10s linear;
  height: 50px;
}
.Top-logo {
  height: 80px;
}
.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}
.Stat-Holder{
  display:-ms-flexbox;
  display:flex;
}

.Stat-Section{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}
.Matches-Holder {
  display:-ms-flexbox;
  display:flex;
  background: white;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

.Collapsable {

}

.MatchBox {
  color: black;
  text-decoration: none;
  background: #bdc3c7;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  margin:5px;
}

.MatchBox:hover{
  background: #95a5a6;;

}

.TeamVersus{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.TagLine {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.Team-Image {
  height:180px;
}

.Team-Image:hover{
  opacity: 0.8
}

a:link    {
  /* Applies to all unvisited links */
  text-decoration:  none;
  font-weight:      normal;
  background-color: #ddd;
  color:            white;
  } 
a:visited {
  /* Applies to all visited links */
  text-decoration:  none;
  font-weight:      normal;
  background-color: #ddd;
  color:            white;
  } 
a:hover   {
  /* Applies to links under the pointer */
  text-decoration:  none;
  font-weight:      normal;
  background-color: #ddd;
  color:            white;
  } 
a:active  {
  /* Applies to activated links */
  text-decoration:  none;
  font-weight:      normal;
  background-color: #ddd;
  color:            white;
  } 

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
} 

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



